import styled from '@emotion/styled';
import { cleanUnderscore, notateNumber, prefix } from 'utility/helpers';
import { Divider, Stack, Typography } from '@mui/material';
import { TitleAndValue } from './styles';
import { getGoldenFoodBonus } from '../../parsers/misc';
import React from 'react';
const ItemDisplay = ({
  character,
  account,
  Type,
  description,
  lvReqToEquip,
  Class: className,
  rawName,
  displayName,
  Defence,
  Speed,
  Weapon_Power,
  Reach,
  STR,
  AGI,
  WIS,
  LUK,
  UQ1txt,
  UQ1val,
  UQ2txt,
  UQ2val,
  Upgrade_Slots_Left,
  desc_line1,
  desc_line2,
  desc_line3,
  desc_line4,
  desc_line5,
  desc_line6,
  desc_line7,
  desc_line8,
  Amount,
  Cooldown,
  capacity,
  capacityPerSlot,
  maxCapacity,
  breakdown
}) => {
  const getPowerType = type => {
    let fixedType = type.toLowerCase();
    if (!fixedType) return 'Weapon Power';
    if (fixedType.includes('mining')) {
      return 'Mining Power';
    } else if (fixedType.includes('fishin')) {
      return 'Fishing Power';
    } else if (fixedType.includes('choppin')) {
      return 'Choppin Power';
    } else if (fixedType.includes('catch')) {
      return 'Catching Power';
    }
    return 'Weapon Power';
  };
  const allDesc = [desc_line1, desc_line2, desc_line3, desc_line4, desc_line5, desc_line6, desc_line7, desc_line8];
  let goldenFoodBonus = 0,
    isGoldenFood = displayName?.includes('Golden');
  if (isGoldenFood) {
    goldenFoodBonus = getGoldenFoodBonus(displayName, character, account);
  }
  const mergedDesc = allDesc.filter(desc => desc !== 'Filler').join(' ').replace(/\[/, isGoldenFood ? notateNumber(goldenFoodBonus, 'Small') : Amount).replace(/]/, Cooldown);
  return displayName && displayName !== 'Empty' && displayName !== 'Locked' ? <>
    <Stack gap={1} direction={'row'} alignItems={'center'} data-sentry-element="Stack" data-sentry-source-file="ItemDisplay.jsx">
      <ItemIcon src={`${prefix}data/${rawName}.png`} alt={displayName} data-sentry-element="ItemIcon" data-sentry-source-file="ItemDisplay.jsx" />
      <Typography fontWeight={'bold'} variant={'subtitle1'} data-sentry-element="Typography" data-sentry-source-file="ItemDisplay.jsx">{cleanUnderscore(displayName)}</Typography>
    </Stack>
    <Divider flexItem sx={{
      my: 2
    }} color={'black'} data-sentry-element="Divider" data-sentry-source-file="ItemDisplay.jsx" />
    {Type?.includes('INVENTORY') || Type?.includes('CARRY') ? <Stack alignitems={'flex-start'}>
        {Type ? <TitleAndValue title={'Type'} value={cleanUnderscore(Type)} /> : null}
        {capacity ? <TitleAndValue title={Type?.includes('CARRY') ? 'Base capacity' : 'Description'} value={`${cleanUnderscore(capacity)}`} /> : null}
        {capacityPerSlot ? <TitleAndValue title={'Capacity per slot'} value={`${notateNumber(capacityPerSlot)}`} /> : null}
        {maxCapacity ? <TitleAndValue title={'Max capacity'} value={`${notateNumber(maxCapacity)}`} /> : null}
      </Stack> : <Stack alignitems={'flex-start'}>
        {Type ? <TitleAndValue title={'Type'} value={cleanUnderscore(Type)} /> : null}
        {capacity ? <TitleAndValue title={'Description'} value={`+${cleanUnderscore(capacity)} slots`} /> : null}
        {description ? <TitleAndValue value={cleanUnderscore(description)} /> : null}
        {mergedDesc.length > 0 ? <TitleAndValue value={cleanUnderscore(mergedDesc)} /> : null}
        {Speed ? <TitleAndValue title={'Speed'} value={Speed} /> : null}
        {Weapon_Power ? <TitleAndValue title={getPowerType(UQ1txt || rawName)} value={Weapon_Power} /> : null}
        {STR ? <TitleAndValue titleStyle={{
        color: 'error.dark'
      }} title={'STR'} value={STR} /> : null}
        {AGI ? <TitleAndValue titleStyle={{
        color: 'success.dark'
      }} title={'AGI'} value={AGI} /> : null}
        {WIS ? <TitleAndValue titleStyle={{
        color: 'secondary.dark'
      }} title={'WIS'} value={WIS} /> : null}
        {LUK ? <TitleAndValue titleStyle={{
        color: 'warning.dark'
      }} title={'LUK'} value={LUK} /> : null}
        {Defence ? <TitleAndValue title={'Defence'} value={Defence} /> : null}
        {Reach ? <TitleAndValue title={'Reach'} value={Reach} /> : null}
        {UQ1txt && UQ1val ? <TitleAndValue title={'Misc'} value={cleanUnderscore(`+${UQ1val}${UQ1txt}`)} /> : null}
        {UQ2txt && UQ2val ? <TitleAndValue title={'Misc'} value={cleanUnderscore(`+${UQ2val}${UQ2txt}`)} /> : null}
        {Upgrade_Slots_Left > 0 ? <TitleAndValue title={'Upgrade Slots Left'} value={Upgrade_Slots_Left} /> : null}
      </Stack>}
    {breakdown ? <>
      <Divider sx={{
        my: 1,
        bgcolor: 'black'
      }} />
      <Stack>
        {breakdown?.map(({
          name,
          value,
          title
        }, index) => title ? <Typography sx={{
          fontWeight: 500
        }} key={`${name}-${index}`}>{title}</Typography> : !name ? <Divider sx={{
          my: 1,
          bgcolor: 'black'
        }} key={`${name}-${index}`} /> : <TitleAndValue key={`${name}-${index}`} titleStyle={{
          width: 120
        }} title={name} value={!isNaN(value) ? notateNumber(value, 'Big') : value} />)}
      </Stack>
    </> : null}
  </> : null;
};
const ItemIcon = styled.img`
  width: 48px;
  height: 48px;
  object-fit: contain;
`;
export default ItemDisplay;